.main-video{
  position: relative;
  width: 100%;
  padding: 160px 0;
  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
    overflow: hidden;
    .dark45 {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3));
      opacity: 1;
      z-index: 444;
    }
    video {
      display: block;
      min-width: 100%;
      position: absolute;
      left: 0;
      width: auto;
      object-fit: cover;
      height: 100%;
    }
  }
  &__text {
    position: relative;
    text-align: center;
    p {
      max-width: 540px;
      font-family: $fontBase;
      font-style: normal;
      font-weight: 500;
      font-size: 16px;
      line-height: 178.9%;
      text-align: center;
      letter-spacing: 0.03em;
      color: $colorMain;
      margin: 0 auto;
    }
    .icon {
      display: inline-block;
      color: #ffffff;
      font-size: 71px;
      cursor: pointer;
      transition: 0.3s linear;
      @include desktop {
        &:hover {
          transform: scale(1.1);
        }
      }
    }
  }
  @include desktop-small{
    padding: 70px 0;
  }
  @include tablet-mobile {
    padding: 30px 0 40px;
  }
}
