    overflow: hidden;
}
.d-block {
    display: block;
}

.inline {
    display: inline !important;
}

.inline-block {
    display: inline-block !important;
    vertical-align: top;
}

.section-grey-bg {
    background-color: $sectionGreyBg;
}
.section.section-grey-bg {
    background-color: $sectionGreyBg;
}

.slick-dots {
    bottom: -60px;
    li {
        border: 2px solid $colorGray;
        box-sizing: border-box;
        border-radius: 50%;
        transition: all 0.3s;
        opacity: 1;
        border: 2px solid rgba(77, 77, 77, 0.5);
        &.slick-active {
            background: $colorButton;
            border: 2px solid $colorButton;
        }
        @include desktop {
            &:hover {
                border: 2px solid $colorButton;
            }
        }

    }
    @include desktop-small{
        bottom: -44px;
    }
    @include tablet-mobile{
        bottom: -25px;
    }
    @include mobile{
        li {
            width: 18px;
            height: 18px;
        }
    }
}
.slick-loading .slick-list
{
    background: #fff !important;
}

.basic-list {
    list-style: none;
    padding-left: 0;
    li {
        position: relative;
        padding-left: 28px;
        font-family: $fontBase;
        font-weight: 500;
        font-size: 16px;
        line-height: 1;
        letter-spacing: 0.03em;
        color: $colorGray;
        margin-bottom: 18px;
        &:before {
            display: block;
            content: '';
            width: 10px;
            height: 10px;
            background: $colorButton;
            position: absolute;
            left: 0;
            top: 3px;
        }
    }
}


.section {
    .submenu {
        text-align: center;
        a {
            display: inline-block;
            text-decoration: none;
            padding: 10px 15px;
            font-family: $fontBase;
            font-weight: 500;
            font-size: 18px;
            line-height: 24px;
            text-align: center;
            color: $colorText;
            background: #F4F4F4;
            transition: background 0.2s linear;
            margin-bottom: 5px;
            &.submenu__active {
                background: $colorButton;
                color: $colorMain
            }
            @include desktop {
                &:hover {
                    background: #EAEAEA;
                    color: $colorText
                }
            }
            &.disabled {
                color: #989898;
            }
        }
    }
}
